import { Component                                            } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController  } from 'ionic-angular';
import { Contacts, User , Developments     , Inputs                                 } from '../../providers';
import { FormBuilder, FormGroup, Validators                   } from '../../../node_modules/@angular/forms';

@IonicPage()
@Component({
  selector: 'page-AddscheduleUndefined',
  templateUrl: 'addschedule-undefined.html',
})

export class AddscheduleUndefinedPage {

  btnAdd      = false;
  spinner     = false;
  data : any  = [] ;
  msg         = '' ;
  public event : any =[];
  public myDate : any;
  public date : any; 
  public time : any;
  public commentForm: FormGroup;
  public monthName: any =[];
  public shortMonthName: any =[];
  public subjet: any =[];
  public devs : any =[];
  constructor(public inputs : Inputs, public developments : Developments, public alertCtrl : AlertController, private user : User, public formBuilder: FormBuilder, public contact: Contacts, public navCtrl: NavController, public viewCtrl : ViewController, public navParams: NavParams) {
    
    ////console.log("addschedule-undefined");
    
    this.myDate = new Date().toISOString();    


    this.date = this.myDate.substring(0,10);
    this.time = this.myDate.substring(11,16);
    this.event.startDate = this.myDate.substring(0, 10);

    this.monthName = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre',  'Noviembre', 'Diciembre' ];
    this.shortMonthName = ['Ene.', 'Feb.', 'Mar.', 'Abr.', 'May.', 'Jun.', 'Jul.', 'Ago.', 'Sep.', 'Oct.', 'Nov.', 'Dic.'];
    this.subjet = ['0', 'Cita', 'Llamada', 'Guardia' , 'Vista de la Propiedad' ,'Inspección' ,'Domiciliaria' ,'Otros',  'Seguimiento'];
    
    this.commentForm = formBuilder.group({
      comment: ['', [Validators.required]],
      day: ['', [Validators.required]],
      hour: [''],
      development: [''],
      state: [''],
      name: ['', [Validators.required]],
      email : [''],
      phone : ['', [Validators.required]]
    });

  } 
  updateDevs() {
    this.devs = [];
    this.developments.developments.forEach((value, key, index) => { 
      ////console.log(value.stateid, '- ', this.data.state);
      
      if(value.stateid == this.data.state){
        ////console.log('yes');
        this.devs.push(value)
      }

    });

  }
  addComment(){

    if(this.btnAdd)
      return;

    this.btnAdd   = true;
    this.spinner  = true;
    var $this     = this;
  
    
    this.data.userid    = this.user.userInfo.userid;
    //this.data.folio     = this.contact.contact.visit.visitid;
    //this.data.officeid  = this.contact.contact.visit.office;
    var response        = this.contact.addCita(this.data);

    Promise.all([response]).then(data => {
      
      this.spinner = false;

      if(data[0]){

        let alert = this.alertCtrl.create({
          title: 'Éxito',
          message: 'Se envió tu solicitud con éxito, un asesor te contactara para confirmar la cita',
          buttons: [
            {
              text: 'Cancelar',
              role: 'cancel',
              handler: () => {

              }
            },
            {
              text: 'Aceptar',
              handler: () => {
                 setTimeout(() => {
                    $this.viewCtrl.dismiss();
                  }, 1000);
              }
            }
          ]
        });
  
        alert.present();
       
      }else{
        $this.btnAdd = false;
      }
   
    }); 
  }

  cancel(){
    this.viewCtrl.dismiss();
  }

}



